<template>
  <div>
    <div id="leixin">
      <!-- <h3 class="title_container">三包退货</h3> -->
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
          <el-form-item label="库存组织" size="mini">
            <el-select clearable v-model="params.company_id" placeholder="请选择" @change="companyChange" @focus="companyListInvok" filterable>
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            <el-select clearable v-model="params.department_id" @focus="DepartmentListInvok" placeholder="请选择" filterable @change="departmentChange">
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="单据号" size="mini">
            <el-input v-model="params.return_sn" placeholder="请输入" @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="客户名称" size="mini">
            <el-select clearable v-model="params.customer_id" @change="getCustomer" filterable placeholder="请输入" @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
              <ul class="customer customer-ul4 customer-ul2">
                <li>客户编号</li>
                <li>客户名称</li>
                <li>联系人</li>
                <li>联系电话</li>
                <li>区域</li>
              </ul>
              <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
                <ul class="customer customer-ul4">
                  <li :title="item.customer_id">{{ item.customer_id }}</li>
                  <li :title="item.customer_name">{{ item.customer_name }}</li>
                  <li :title="item.contact_name">{{item.contact_name}}</li>
                  <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                  <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
                </ul>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="出库单号" size="mini">
            <el-input v-model="params.outstock_sn" placeholder="请输入" @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <!-- <el-form-item label="审核日期" size="mini">
            <el-date-picker v-model="auditValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item> -->
          <el-form-item label="单据状态" size="mini">
            <el-select clearable v-model="params.auditor_status">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch(1)" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="salesAddF" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
        <el-button icon="iconfont icon-pass" type="primary" @click="examineFun" size="mini" v-if="authority.b_pass==1">审核</el-button>
        <el-button plain icon="iconfont icon-zuofei" @click="voidF" size="mini" v-if="authority.b_invalid==1">作废</el-button>
        <el-button plain icon="iconfont icon-fanshenhe" v-if="authority.b_nopass == 1" @click="cancellation" size="mini">弃审</el-button>
        <el-button plain size="mini" icon="el-icon-printer" v-if="authority.b_print==1" @click="printF">批量打印</el-button>
      </div>
      <xTable :ajax='{ url: "getThressReturnList", params: params }' ref='xTable' :tableColumn='tableColumn' :xData='tableData' />
      <div v-if="dialogUserVisible">
        <el-dialog :close-on-click-modal="false" title="审核确认" class="dialogForm" :visible.sync="dialogUserVisible" width="800px">
          <div style="width: 750px;">
            <span style="color:red">提示：由于以下商品无结存成本或者结存成本为0，请先输入成本单价！</span>
            <el-table :highlight-current-row="true" tooltip-effect="light" border :data="leftList" height="242" style="width: 750px;float:left;margin-top:5px;cursor:pointer">
              <el-table-column :show-overflow-tooltip="true" property="company_name" width="100" label="销售组织"></el-table-column>
              <el-table-column :show-overflow-tooltip="true" property="warehouse_name" width="132" label="仓库"></el-table-column>
              <el-table-column :show-overflow-tooltip="true" property="allocation_name" width="100" label="货位"></el-table-column>
              <el-table-column :show-overflow-tooltip="true" property="goods_id" width="100" label="商品编号"></el-table-column>
              <el-table-column :show-overflow-tooltip="true" property="goods_name" width="138" label="商品名称"></el-table-column>
              <el-table-column :show-overflow-tooltip="true" property="batch" width="100" label="年周/批次"></el-table-column>
              <el-table-column :show-overflow-tooltip="true" property="cost_price" width="110" label="成本单价/条" :render-header="renderHeader">
                <template slot-scope="scope">
                  <el-input size="small" type="number" @change="getcost(scope.row,scope.$index)" v-model="scope.row.cost_price" suffix-icon="el-icon-edit-outline" placeholder="请输入"></el-input>
                </template>
              </el-table-column>
            </el-table>
          </div>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogUserVisible=false" style="margin-top:10px">取 消</el-button>
            <el-button class="tx" type="primary" @click="submitUserForm()" style="margin-top:10px">确 定</el-button>
          </div>
        </el-dialog>
      </div>
      <publicPrint ref="printDelieveryBill" @Search="onSearch" title="threeReturn"></publicPrint>
    </div>

  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import { mixins } from "./config/threePackageReturnIndex";
import publicPrint from "common/components/public/PublicPrint.vue"; //公共打印
export default {
  name: "salesSalesReturnIndex",
  mixins: [mixins],
  data() {
    return {
      printFlag: false,
      leftList: [],
      loading:false,
      params: {
        auditor_status: 0,
        outstock_type_id: 0
      },
      timeValue2: [],
      dialogUserVisible: false,
      auditValue: null,
      hasAuditorStatusPrint: null
    };
  },
  computed: {
    ...mapGetters(["salesReturnIndexGet"])
  },
  components: {
    publicPrint
  },
  methods: {
    ...mapActions([
      "salesReturnAdd",
      "salesReturnSee",
      "salesReturnModify",
      "salesReturnIndexACT"
    ]),
    printF() {
      let companyFlag = false;
      const companyIds = this.multipleSelection;
      for (var i = 1; i < companyIds.length; i++) {
        if (companyIds[i - 1].company_id != companyIds[i].company_id) {
          companyFlag = true;
          break;
        }
      }
      if (!this.multipleSelection.length) {
        this.$message({
          message: "请选择单据进行打印",
          type: "warning"
        });
      } else if (companyFlag) {
        this.$message({
          message: "请选择相同公司单据进行打印",
          type: "warning"
        });
      } else {
        let flag = true,
          errorGoodsArr = [];
        this.multipleSelection.forEach(item => {
          if (item.after_auditor_print_status == 1) {
            //审核后才可打印
            if (item.auditor_status != 2) {
              errorGoodsArr.push(item.return_sn);
              flag = false;
            }
          }
        });

        if (flag) {
          this.$refs.printDelieveryBill.printList(this.multipleSelection, [
            "return_id"
          ]);
        } else {
          let str = errorGoodsArr.map((item, i) => `${i + 1}.${item}`);
          this.$alert(
            `以下未审核的单据不可打印：<br/>
            <div style="color:#e6a23c">
              ${str.join("，")}
            <div>`,
            "提示",
            {
              dangerouslyUseHTMLString: true
            }
          );
        }
      }
    },
    cancellation() {
      //弃审
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: `请选择需要弃审的信息`,
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].return_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "return_id",
        eachData: {
          return_id: rebateArr
        },
        examineApi: "auditNoThressReturn",
        list: {
          UrlApi: "getReturnList",
          data: this.tableData,
          esData: this.searchParams.params
        }
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 7,
          bill_id: addr.join(","),
          operation_type: 6
        })
        .then(() => {
          this.$common.examineFun(funcan, this.onSearch);
        });
    },
    printSheet(row, index = 0) {
      this.$nextTick(function() {
        this.$refs.printDelieveryBill.printPdf(
          {
            return_id: row.return_id
          },
          index
        );
      });
    },
    getcost(row, index) {
      if (row.cost_price < 0 || isNaN(row.cost_price)) {
        this.$message.warning("成本单价不能为空且必须为正数");
        this.$set(this.leftList[index], "cost_price", "");
      } else if (row.cost_price >= 1e8) {
        this.$message.warning("数值不能超过一亿");
        this.$set(this.leftList[index], "cost_price", 0);
      } else {
        let a = parseFloat(Number(row.cost_price)).toFixed(2);
        this.$set(this.leftList[index], "cost_price", a);
      }
    },
    renderHeader(h, { column, $index }) {
      return (
        <span class="start" style="cursor:pointer">
          {" "}
          {column.label}{" "}
        </span>
      );
    },
    getCustomer(val) {
      if (val != "") {
        this.params.customer_name = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].customer_name;
      }
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
    },
    selectRowChangeBg({ row, rowIndex }) {
      if (
        this.multipleSelection.length &&
        this.multipleSelection.map(obj => obj.return_id).includes(row.return_id)
      ) {
        return "background:#d4e1ee";
      } else {
        return "";
      }
    },
    // 详情
    seeDetail(row) {
      this.$router.push({
        path: "/aftersale/threepackagereturn/threePackageReturnSee",
        query: { id: row.return_id }
      });
    },
    examineFun() {
      let vm = this;
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].return_id);
      }
      var return_id = addr.toString();
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 7,
          bill_id: return_id,
          operation_type: 4
        })
        .then(() => {
          this.$axMethod
            .checkThreeReturnGoods({
              return_id: return_id
            })
            .then(({ data }) => {
              if (data.code == 200) {
                if (data.data.is_success == 1) {
                  var funcan = {
                    examineID: "return_id",
                    eachData: {
                      return_id: return_id
                    },
                    examineApi: "auditThressReturn",
                    list: {
                      UrlApi: "getReturnList",
                      data: this.tableData,
                      esData: this.searchParams.params
                    }
                  };
                  vm.$common.examineFun(funcan, this.onSearch);
                } else {
                  this.leftList = data.data;
                  this.dialogUserVisible = true;
                }
              }
            });
        });
    },
    submitUserForm() {
      for (var i = 0; i < this.leftList.length; i++) {
        if (
          this.leftList[i].cost_price == undefined ||
          this.leftList[i].cost_price == null ||
          this.leftList[i].cost_price == ""
        ) {
          this.$message({
            message: "成本单价不能为空且必须为正数",
            type: "warning"
          });
          return false;
        }
      }

      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].return_id);
      }
      var return_id = addr.toString();
      var funcan = {
        examineID: "return_id",
        eachData: {
          return_id: return_id,
          cost_price_str: JSON.stringify(this.leftList)
        },
        examineApi: "auditThressReturn",
        list: {
          UrlApi: "getReturnList",
          data: this.tableData,
          esData: this.searchParams.params
        }
      };
      this.$common.examineFun(funcan, this.onSearch);
      this.dialogUserVisible = false;
    },
    voidF() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要作废的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].return_id);
      }
      var return_id = addr.toString();
      var funcan = {
        examineID: "return_id",
        eachData: {
          return_id: return_id
        },
        examineApi: "auditInvalidThressReturn",
        list: {
          UrlApi: "getReturnList",
          data: this.tableData,
          esData: this.searchParams.params
        }
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 7,
          bill_id: addr.join(","),
          operation_type: 6
        })
        .then(() => {
          this.$common.examineFun(funcan, this.onSearch);
        });
    },
    salesModifyF(scope, row) {
      if (row && row.is_execute == 1) {
        this.$message.error("wms系统正在操作此单据，暂不支持单据修改");
        return false;
      }
      this.timeF();
      this.$router.push({ path: "/sales/salesrefund/salesReturnAdd" });
      this.salesReturnAdd({
        return_id: row.return_id,
        departmentList: this.departments,
        companyList: this.companys,
        SelesTomerList: this.customerList,
        params: this.params,
        goPage: this.params.goPage,
        pageSize: this.params.pageSize
      });
    },
    // 新增修改
    salesAddF(row) {
      this.$router.push({
        path: "/aftersale/threepackagereturn/threepackagereturnAdd",
        query: {
          id: row.return_id
        }
      });
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {
        auditor_status: 0,
        outstock_type_id: 0
      });
      this.timeValue = [];
      this.timeValue2 = [];
      this.auditValue = [];
      this.onSearch(1);
    },
    timeF() {
      if (this.timeValue) {
        this.params.business_start_time = this.timeValue[0];
        this.params.business_end_time = this.timeValue[1];
      } else {
        this.params.business_start_time = this.params.business_end_time = "";
      }
      if (this.timeValue2) {
        this.params.add_start_time = this.timeValue2[0];
        this.params.add_end_time = this.timeValue2[1];
      } else {
        this.params.add_start_time = this.params.add_end_time = "";
      }
      if (this.auditValue) {
        this.params.auditor_start_time = this.auditValue[0];
        this.params.auditor_end_time = this.auditValue[1];
      } else {
        this.params.auditor_start_time = this.params.auditor_end_time = "";
      }
    },
    onSearch(number) {
      this.timeF();
      this.$refs.xTable._getData(number);
    },
    //删除
    deletes() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].return_id);
      }
      var deledata = {
        deleID: "return_id",
        deleSelect: this.multipleSelection,
        deleApi: "deleteThressReturn",
        list: {
          UrlApi: "getReturnList",
          data: this.tableData,
          esData: this.searchParams.params
        }
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 7,
          bill_id: addr.join(","),
          operation_type: 3
        })
        .then(() => {
          this.$common.deletes(deledata, "删除", this, this.onSearch);
        });
    },
    companyChange(val) {
      if (val != "") {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "department_id", "");
      this.$set(this.params, "customer_id", "");
    }
  },
  created() {
    this.companyList.push({
      company_name: (this.params.company_name = localStorage.getItem(
        "company_name"
      )),
      company_id: (this.params.company_id = localStorage.getItem("company_id"))
    });
    this.hasAuditorStatusPrint = window.localStorage.hasAuditorStatusPrint;
  },
  activated() {
    this.onSearch(window.goPage);
  }
};
</script>

<style scoped>
</style>
